@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Roboto', sans-serif;
}

.app {
	padding-top: 5rem;
	display: flex;
	justify-content: center;
}

.wrap {
	width: 500px;
}

$height: 46px;

.select {
	width: 100%;
	position: relative;
	z-index: 100;

	&.open {
		.select {
			&__dropdown {
				display: block;
			}
			&__input {
				border-bottom: none;
			}
			&__backdrop {
				display: block;
			}
		}

	}

	&__input {
		border: 1px solid #ccc;
		border-radius: 5px;
		height: $height;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 1rem;
		cursor: pointer;
	}

	&__dropdown {
		display: none;
		position: absolute;
		border: 1px solid #ccc;
		border-radius: 5px;
		top: $height;
		left: 0;
		right: 0;
		max-height: 200px;
		overflow-y: auto;
		box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
	}

	&__list {
		padding: 0;
		margin: 0;
		list-style: none;
	}

	&__backdrop {
		position: fixed;
		display: none;
		z-index: -1;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		background: transparent;
	}

	&__item {
		padding: 1rem;
		border-bottom: 1px solid #ccc;

		&.selected {
			background-color: bisque;
		}

		&:hover {
			background-color: #eee;
			cursor: pointer;
			transition: 0.15s background-color ease-in;
		}
	}
}
